import React, {Component} from 'react'
import inputCss from './Input.module.css'

class Input extends Component {

    cls = [
        inputCss.formItem
    ]

    htmlFor = Math.random()

    isInvalid({valid, touched, shouldValidate}) {
        return !valid && shouldValidate && touched
    }

    render() {
        const isInvalid = this.isInvalid(this.props)

        return (
            <div className={this.cls.join(' ') + ` ${isInvalid ? inputCss.invalid: inputCss.valid}`}>
                <label htmlFor={this.htmlFor}>{this.props.label}</label>
                <input
                    id={this.htmlFor}
                    type={this.props.type || 'text'}
                    value={this.props.value}
                    onChange={this.props.changeInputHandler}
                    placeholder={this.props.placeholder}
                    autoComplete="on"
                />

                {
                    isInvalid ? <span>{this.props.errorMessage}</span> : null
                }
            </div>
        )
    }
}

export default Input
