import {ACTIVE, CHANGE} from "../actions/actionsTypes";

const initialState = {
    isFormValid: false,
    activeForm: 'login',
    formControls: {
        email: {
            value: '',
            type: 'email',
            touched: false,
            valid: false,
            label: 'Email',
            placeholder: 'Write your email',
            errorMessage: 'Введите корректный email',
            validation: {
                required: true,
                email: true
            }
        },
        password: {
            value: '',
            type: 'password',
            touched: false,
            valid: false,
            label: 'Password',
            placeholder: 'Write your password',
            errorMessage: 'Введите корректный пароль',
            validation: {
                required: true,
                minLength: 6,
                maxLength: 15
            }
        },
        passwordConfirm: {
            value: '',
            type: 'password',
            touched: false,
            valid: false,
            label: 'Confirm password',
            placeholder: 'Confirm your password',
            errorMessage: 'Введите корректный пароль',
            validation: {
                required: true,
                minLength: 6,
                maxLength: 15
            }
        }
    }
}

export default function loginReducer(state = initialState, action) {

    switch (action.type) {
        case CHANGE:
            return {
                ...state,
                isFormValid: action.payload.isFormValid,
                formControls: action.payload.formControls,
            }
        case ACTIVE:
            return {
                ...state,
                activeForm: action.payload
            }
        default:
            return state
    }
}
