import React, {Component} from 'react'
import todoCss from './Todo.module.css'

import upArrow from '../../assets/icons/up-arrow.svg'
import downArrow from '../../assets/icons/down-arrow.svg'
import editImg from '../../assets/icons/edit.svg'
import checkImg from '../../assets/icons/check.svg'
import removeImg from '../../assets/icons/delete.svg'

class Todo extends Component {

    getDate(ts) {
        let str
        ts = new Date(ts)

        str = `${ts.getDate().toString().length === 1 ? '0' + ts.getDate() : ts.getDate()}.`
        str += `${ts.getMonth() + 1 === 1 ? '0' + (ts.getMonth() + 1) : ts.getMonth() + 1}.`
        str += `${ts.getFullYear()}`

        return str
    }

    getTime(ts) {
        ts = new Date(ts)
        return `${ts.getHours()}:${ts.getMinutes()}`
    }

    render() {
        if (this.props.hide) {
            return null
        }

        const date = this.props.date ? this.props.date : Date.now()

        const fullDate = {
            date: this.getDate(date),
            time: this.getTime(date)
        }

        const clsText = [
            todoCss.text
        ]

        const clsChecked = [
            todoCss.circle,
            todoCss.unchecked
        ]

        if (this.props.completed) {
            clsText.push(todoCss.completed)
            clsChecked.push(todoCss.checked)
        }

        return (
            <div className={todoCss.container}>
                <div className={todoCss.Todo}>
                    <div className={todoCss.date}>
                        <p>{fullDate['date']}</p>
                        <p>{fullDate['time']}</p>
                    </div>
                    <div className={todoCss.priority}>
                        {this.props.priority}
                    </div>
                    <div className={todoCss.arrows}>
                        <img
                            src={upArrow}
                            className={todoCss.arrow}
                            alt="Up Arrow"
                            onClick={this.props.upPriorityHandler}
                        />
                        <img
                            src={downArrow}
                            className={todoCss.arrow}
                            alt="Up Arrow"
                            onClick={this.props.downPriorityHandler}
                        />
                    </div>
                    <div className={clsText.join(' ')}>
                        {this.props.title}
                    </div>
                    <div>
                        <div
                            className={`${todoCss.circle} ${todoCss.edit}`}
                            onClick={this.props.editHandler}
                        >
                            <img className={todoCss.img} src={editImg} alt="Edit"/>
                        </div>
                    </div>
                    <div>
                        <div
                            className={clsChecked.join(' ')}
                            onClick={this.props.checkHandler}
                        >
                            <img className={todoCss.img} src={checkImg} alt="Check"/>
                        </div>
                    </div>
                    <div>
                        <div
                            className={`${todoCss.circle} ${todoCss.delete}`}
                            onClick={this.props.deleteHandler}
                        >
                            <img className={todoCss.img} src={removeImg} alt="Remove"/>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Todo
