import React, {Component} from 'react'
import loginCss from './Login.module.css'
import Input from "../components/UI/Input/Input";
import Button from "../components/UI/Button/Button";
import {isAuth, login, register} from '../modules/login'
import is from 'is_js'
import {connect} from "react-redux";
import {changeActive, changeFormAction} from "../redux/actions/actions";

class Login extends Component {

    keys = {
        email: 'email',
        password: 'password',
        passwordConfirm: 'passwordConfirm'
    }

    componentDidMount() {
        if (isAuth()) {
            this.props.history.push({
                pathname: '/todos'
            })
        }
    }

    validateControl(value, validation) {
        if (!validation) {
            return true
        }

        let isValid = true

        if (validation.required) {
            isValid = value.trim() !== '' && isValid
        }

        if (validation.email) {
            isValid = is.email(value) && isValid
        }

        if (validation.minLength) {
            isValid = value.length >= validation.minLength && isValid
        }

        if (validation.maxLength) {
            isValid = value.length <= validation.maxLength && isValid
        }

        return isValid
    }

    changeHandler = (event, controlName) => {
        const value = event.target.value

        let isFormValid = true

        const names = [
            this.keys.email,
            this.keys.password
        ]

        if (this.props.activeForm === 'reg') names.push(this.keys.passwordConfirm)

        const formControls = {...this.props.formControls}
        formControls[controlName].valid = this.validateControl(value, formControls[controlName].validation)
        formControls[controlName].value = value
        formControls[controlName].touched = true

        names.forEach(name => {
            isFormValid = formControls[name].valid && isFormValid
        })

        this.props.changeForm(isFormValid, formControls)
    }

    changeActiveForm = (type) => {
        this.props.changeActive(type)
        
        Object.keys(this.props.formControls).map((controlName) => {
            const prevFormControls = this.props.formControls
            prevFormControls[controlName].value = ''
            prevFormControls[controlName].touched = false
            
            this.props.changeForm(false, prevFormControls)
        })
    }

    loginHandler = (event) => {
        event.preventDefault()

        const user = {
            email: this.props.formControls[this.keys.email].value.toString().trim(),
            password: this.props.formControls[this.keys.password].value.toString().trim()
        }

        const status = login(user)

        if (status) {
            this.props.history.push({
                pathname: '/todos'
            })
        } else {
            alert('User not found!')
        }

    }

    registerHandler = (event) => {
        event.preventDefault()

        const newUser = {
            email: this.props.formControls[this.keys.email].value.toString().trim(),
            password: this.props.formControls[this.keys.password].value.toString().trim(),
            passwordConfirm: this.props.formControls[this.keys.passwordConfirm].value.toString().trim(),
        }

        const status = register(newUser)

        if (status) this.props.history.push({
            pathname: '/todos'
        })
    }

    getInput(key) {
        const control = this.props.formControls[key]

        return (
            <Input
                type={control.type}
                value={control.value}
                changeInputHandler={(e) => this.changeHandler(e, key)}
                label={control.label}
                placeholder={control.placeholder}
                valid={control.valid}
                errorMessage={control.errorMessage}
                touched={control.touched}
                shouldValidate={!!control.validation}
            />
        )
    }

    render() {

        const regBox = (
            <React.Fragment>
                {this.getInput(this.keys.passwordConfirm)}
                <Button
                    text={'Register'}
                    btnType={'btn-register'}
                    btnClick={(e) => this.registerHandler(e)}
                    disabled={!this.props.isFormValid}
                />
            </React.Fragment>
        )

        const loginBox = (
            <Button
                text={'Login'}
                btnType={'btn-login'}
                btnClick={(e) => this.loginHandler(e)}
                disabled={!this.props.isFormValid}
            />
        )

        return (
            <div className={loginCss.LoginContainer}>
                <div className={loginCss.Login}>
                    <form className={loginCss.form}>
                        <legend>
                            <span
                                className={this.props.activeForm === 'login' ? loginCss.active : null}
                                onClick={this.changeActiveForm.bind(this, 'login')}
                            >Login</span>

                            <span
                                className={this.props.activeForm === 'reg' ? loginCss.active : null}
                                onClick={this.changeActiveForm.bind(this, 'reg')}
                            >Register</span>
                        </legend>

                        {this.getInput(this.keys.email)}

                        {this.getInput(this.keys.password)}

                        {
                            this.props.activeForm === 'reg' ? regBox : loginBox
                        }

                    </form>
                </div>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        activeForm: state.loginReducer.activeForm,
        formControls: state.loginReducer.formControls,
        isFormValid: state.loginReducer.isFormValid,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        changeForm: (isFormValid, formControls) => dispatch(changeFormAction(isFormValid, formControls)),
        changeActive: (type) => dispatch(changeActive(type))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Login)
