import {ACTIVE, CHANGE, CLOSE, DELETE, EDIT, SET, TRG_DELETE, TRG_EDIT} from "./actionsTypes";


export function showDeleteAlert() {
    return {
        type: DELETE
    }
}

export function showEditAlert() {
    return {
        type: EDIT
    }
}

export function closeAlert() {
    return {
        type: CLOSE
    }
}

export function triggerEditAlert(id, text) {
    return {
        type: TRG_EDIT,
        payload: {id, text}
    }
}

export function triggerDeleteAlert(id) {
    return {
        type: TRG_DELETE,
        payload: id
    }
}

export function setTodosAction(todos) {
    return {
        type: SET,
        payload: todos
    }
}

export function changeFormAction(isFormValid, formControls) {
    return {
        type: CHANGE,
        payload: {isFormValid, formControls}
    }
}


export function changeActive(type) {
    return {
        type: ACTIVE,
        payload: type
    }
}
