import React, {Component} from 'react'
import filtersCss from './Filters.module.css'

import sortPriority from '../../assets/icons/sort-priority.svg'
import sortDate from '../../assets/icons/sort-arrows.svg'
import sortChecked from '../../assets/icons/filter.svg'
import searchImg from '../../assets/icons/search.svg'

class Filters extends Component {

    render() {
        return (
            <div className={filtersCss.container}>
                <div>
                    <div>
                        <img
                            className={filtersCss.img}
                            src={sortDate}
                            alt="Sort by date"
                            onClick={this.props.dateSortHandler}
                            style={{
                                marginLeft: '-55px'
                            }}
                        />
                    </div>
                    <div>
                        <img
                            className={filtersCss.img}
                            src={sortPriority}
                            alt="Sort by priority"
                            onClick={this.props.prioritySortHandler}
                            style={{
                                marginLeft: '-47px'
                            }}
                        />
                    </div>
                    <div className={filtersCss.search}>
                        <img src={searchImg} alt="Search Todo"/>
                        <input
                            type="text"
                            onChange={this.props.searchHandler}
                            style={{
                                marginLeft: '-24px'
                            }}
                        />
                    </div>
                    <div>
                        <img
                            className={filtersCss.img}
                            src={sortChecked}
                            alt="Sort by checked"
                            onClick={this.props.checkedSortHandler}
                            style={{
                                marginRight: '-25px'
                            }}
                        />
                    </div>
                </div>
            </div>
        )
    }
}

export default Filters
