import React, {Component} from 'react'
import rebuildCss from './Rebuild.module.css'
import {removeUserAuth} from '../modules/login'
import {clearTodos} from '../modules/todo'

class Rebuild extends Component {

    rebuild = () => {
        removeUserAuth()
        clearTodos()
        this.props.history.push({
            pathname: '/'
        })
    }

    render() {
        return (
            <button
                className={rebuildCss.Rebuild}
                onClick={this.rebuild}
            >REBUILD</button>
        )
    }
}

export default Rebuild
