import {CLOSE, DELETE, EDIT, TRG_DELETE, TRG_EDIT} from "../actions/actionsTypes";

const initialState = {
    isShow: false,
    type: '',
    message: '',
    deleteId: null,
    editId: null,
    editText: ''
}

export default function alertReducer(state = initialState, action) {

    switch (action.type) {
        case DELETE:
            return {
                ...state,
                isShow: true,
                type: 'delete'
            }
        case EDIT:
            return {
                ...state,
                isShow: true,
                type: 'edit'
            }
        case CLOSE:
            return {
                ...state,
                isShow: false,
                deleteId: null,
                editId: null
            }
        case TRG_DELETE:
            return {
                ...state,
                deleteId: action.payload
            }
        case TRG_EDIT:
            return {
                ...state,
                editId: action.payload.id,
                editText: action.payload.text
            }
        default:
            return state
    }
}
