import React, {Component} from 'react'
import addCss from './Add.module.css'

class Add extends Component {

    newTodoTitleValue = ''
    myRef = React.createRef()

    writeNewTodoTitle = (event) => {
        this.newTodoTitleValue = event.nativeEvent.target.value.toString().trim()
    }

    clickHandler = () => {
        this.props.addTodoHandler(this.newTodoTitleValue)
        console.log(this.myRef)
        this.newTodoTitleValue = ''
        this.myRef.current.value = ''
    }

    render() {
        return (
            <div
                className={addCss.add}
            >
                <input
                    type="text"
                    placeholder={'Task'}
                    onChange={this.writeNewTodoTitle}
                    ref={this.myRef}
                />
                <button
                    onClick={this.clickHandler}
                >ADD</button>
            </div>
        )
    }
}

export default Add
