import React, {Component} from 'react'
import AlertContext from './AlertContext'
import alertCss from './Alert.module.css'
import {connect} from "react-redux";
import {
    closeAlert,
    showDeleteAlert,
    showEditAlert,
    triggerDeleteAlert,
    triggerEditAlert
} from "../../redux/actions/actions";

class Alert extends Component {

    myRef = React.createRef()

    info = {
        todoId: null,
        todoText: null
    }

    maxLength = 100
    minLength = 5

    componentDidUpdate() {
        if (this.props.type === 'edit' && this.myRef && this.info.todoText) {
            this.myRef.current.innerHTML = this.maxLength - this.info.todoText.length
        }
    }

    showDeleteAlert = id => {
        this.info.todoId = id

        this.props.showDelete()
    }

    showEditAlert = (id, title) => {
        this.info = {
            todoId: id,
            todoText: title
        }

        this.props.showEdit()
    }

    changeTextHandler = (event) => {
        const value = event.nativeEvent.target.value.toString().trim()

        this.info.todoText = value
        this.myRef.current.innerHTML = this.maxLength - value.length
    }

    completeEdit = () => {
        this.info = {
            todoId: null,
            todoText: null
        }

        this.props.closeModal()
    }

    triggerEdit = () => {
        const length = this.info.todoText.length

        if (length < this.minLength || length > this.maxLength) {
            alert(`Invalid string length! The correct length is ${this.minLength} to ${this.maxLength} characters.`)
            return
        }

        this.props.triggerEdit(this.info.todoId, this.info.todoText)
    }

    triggerDelete = () => {
        this.props.triggerDelete(this.info.todoId)
    }

    completeDelete = () => {
        this.info.todoId = null

        this.props.closeModal()
    }

    render() {
        return (
            <React.Fragment>
                <AlertContext.Provider value={{
                    state: {
                        type: this.props.type,
                        editId: this.props.editId,
                        deleteId: this.props.deleteId,
                        editText: this.props.editText
                    },
                    showDeleteAlert: this.showDeleteAlert,
                    completeDelete: this.completeDelete,
                    showEditAlert: this.showEditAlert,
                    completeEdit: this.completeEdit
                }}>
                    {this.props.children}
                </AlertContext.Provider>
                {
                    this.props.isShow && this.props.type === 'delete'
                        ? <div className={alertCss.container}>
                            <div className={alertCss.delete}>
                                <h2 className={alertCss.delete__title}>Delete item?</h2>
                                <div className={alertCss.delete__buttons}>
                                    <button
                                        className={alertCss.delete__button}
                                        onClick={this.triggerDelete}
                                    >YES
                                    </button>
                                    <button
                                        className={`${alertCss.delete__button} ${alertCss.delete__buttons_no}`}
                                        onClick={this.completeDelete}
                                    >NO
                                    </button>
                                </div>
                            </div>
                        </div>
                        : null
                }

                {
                    this.props.isShow && this.props.type === 'edit'
                        ? <div className={alertCss.container}>
                            <div className={alertCss.edit}>
                                <h2 className={alertCss.edit__title}>Edit text</h2>
                                <textarea
                                    className={alertCss.edit__area}
                                    defaultValue={this.info.todoText}
                                    onChange={this.changeTextHandler}
                                />
                                <span>
                                Characters limit:
                                    <p className={alertCss.edit__count} ref={this.myRef}/>
                            </span>
                                <div className={alertCss.edit__buttons}>
                                    <button
                                        className={alertCss.edit__button + ' ' + alertCss.edit__button_cancel}
                                        onClick={this.completeEdit}
                                    >CANCEL
                                    </button>
                                    <button
                                        className={alertCss.edit__button}
                                        onClick={this.triggerEdit}
                                    >SAVE
                                    </button>
                                </div>
                            </div>
                        </div>
                        : null
                }
            </React.Fragment>
        )
    }
}

function mapStateToProps(state) {
    return {
        isShow: state.alertReducer.isShow,
        type: state.alertReducer.type,
        message: state.alertReducer.message,
        deleteId: state.alertReducer.deleteId,
        editId: state.alertReducer.editId,
        editText: state.alertReducer.editText
    }
}

function mapDispatchToProps(dispatch) {
    return {
        showDelete: () => dispatch(showDeleteAlert()),
        showEdit: () => dispatch(showEditAlert()),
        closeModal: () => dispatch(closeAlert()),
        triggerEdit: (id, text) => dispatch(triggerEditAlert(id, text)),
        triggerDelete: (id) => dispatch(triggerDeleteAlert(id))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Alert)
